import Injector from 'lib/Injector';

Injector.transform(
  'require-alt-text',
  (updater) => {
    updater.form.addValidation(
      'AssetAdmin.*',
      (values, Validation) => {
        console.log('hi2');
        const alt = (!values.AltText || values.AltText.length < 1) ? 'Alt text is required' : null

        if (alt) {
          Validation.addError('AltText', alt);
        }
      }
    );
  }
);



